<script>
//获取点击结点的值
import { toDaynodeData } from "@/components/ElectricEnergyAnalysis.vue";
import axios from "axios";

export default {
  name: "LineChart",
  data() {
    return {
      tableData0:[],
      Daydata: [],
      Monthdata: [],
      Yeardata: [],
      //选择框定义
      areshow0: "",
      areshow1: "display:none",
      areshow2: "display:none",
      areshow3: "display:none",
      data: [
        {
          id: 1,
          label: "电能管理体验馆",
          children: [
            {
              id: 2,
              label: "车间总",
              children: [
                {
                  id: 3,
                  label: "产线2",
                  children: [
                    {
                      id: 4,
                      label: "3#动力柜",
                    },
                    {
                      id: 5,
                      label: "风机系统",
                    },
                  ],
                },
              ],
            },
            {
              id: 6,
              label: "公共设备",
              children: [
                {
                  id: 7,
                  label: "冰机",
                  children: [
                    {
                      id: 8,
                      label: "1#空调柜2",
                    },
                    {
                      id: 8,
                      label: "空调用水泵",
                    },
                    {
                      id: 8,
                      label: "2#冰水机",
                    },
                    {
                      id: 8,
                      label: "2#空调柜",
                    },
                    {
                      id: 9,
                      label: "1#冰水机",
                    },
                  ],
                },
                {
                  id: 7,
                  label: "空压机",
                  children: [
                    {
                      id: 8,
                      label: "高压空压机",
                    },
                    {
                      id: 8,
                      label: "低压空压机",
                    },
                  ],
                },
              ],
            },
            {
              id: 2,
              label: "变压器",
              children: [
                {
                  id: 3,
                  label: "主变",
                },
              ],
            },
          ],
        },
      ],
      defaultProps: {
        children: "children",
        label: "label",
      },
      content: toDaynodeData,
//后端数据容器
      TABLEdata: [], //图表
      //曲线选择
      options: [
        {
          value: "日电量明细",
        },
        {
          value: "月电量明细",
        },
        {
          value: "年电量明细",
        },
        {
          value: "自定义日统计",
        },
      ],
      //曲线参数
      value2: "日电量明细",
      //日期选择器
      daypick: null,
      monthpick: null,
      yearpick: null,

      //时间参数
      value1: "",
      disabledDate(time) {
        return time.getTime() > Date.now();
      },
      tableData: [],
    };
  },

  created() {},
  mounted() {
    this.convert();
  },

  methods: {
    //打印PDF
    handleCommand(command) {
      if (command == "a") {
        this.getPdf();
      }
    },
    //下拉框选择
    changeTable() {
      //this.convert()
      console.log(this.daypick)
      if (this.value2 == "日电量明细") {
        //负荷变量赋值
        //选择框赋值

        (this.areshow0 = ""),
          (this.areshow1 = "display:none"),
          (this.areshow2 = "display:none"),
          (this.areshow3 = "display:none");

        if (this.daypick == null) {
          this.$message({
            message: "请选择日期！！！",
            type: "warning",
          });
        } else {
          let n = 0;
          // while (n < this.TABLEdata.length) {
          let daypowerNameList = [];
          let daypowerList = [];
          let i = 0;
          for (var key in this.Daydata) {
            if (key != "" && key != null && !isNaN(key)) {
              daypowerList.push(this.Daydata[key]);
            }
          }
          for (var key in this.Daydata.devicesName) {
            daypowerNameList.push(this.Daydata.devicesName[key]);
          }
          while (n < daypowerList.length) {
            this.tableData0.push({
              deviceId1: "",
              t0: "",
              t1: "",
              t2: "",
              t3: "",
              t4: "",
              t5: "",
              t6: "",
              t7: "",
              t8: "",
              t9: "",
              t10: "",
              t11: "",
              t12: "",
              t13: "",
              t14: "",
              t15: "",
              t16: "",
              t17: "",
              t18: "",
              t19: "",
              t20: "",
              t21: "",
              t22: "",
              t23: "",
            });
            n = n + 1;
          }
          console.log(this.daypowerList);
          while (i < daypowerList.length) {
            // this.tableData1[i].deviceId1 = this.TABLEdata[i];
            let flag = 0;
            for (var key in this.tableData0[i]) {
              if (key != "deviceId1") {
                this.tableData0[i][key] = daypowerList[i][flag].totalKwh;
                flag += 1;
              }
            }

            (this.tableData0[i].deviceId1 = daypowerNameList[i]), (i = i + 1);
          }
        }
      } else if (this.value2 == "月电量明细") {
        //给无功功率赋值
        //选择框赋值
        this.areshow0 = "display:none";
        this.areshow1 = "";
        this.areshow2 = "display:none";
        this.areshow3 = "display:none";

        if (this.monthpick == null) {
          this.$message({
            message: "请选择日期！！！",
            type: "warning",
          });
        } else {
          let n = 0;
          // while (n < this.TABLEdata.length) {
        }
      } else if (this.value2 == "年电量明细") {
        //给功率因数赋值
        //选择框赋值
        (this.areshow0 = "display:none"),
          (this.areshow1 = "display:none"),
          (this.areshow2 = ""),
          (this.areshow3 = "display:none");
      }
    },

    convert() {
      let parms = this.$qs.stringify({
        deviceIds: "1,3,4",
        start: "1638288001",
        end: "1638374400",
        unit: "1",
      });
      //请求表格数据
      axios
        .post("http://121.43.111.195/powerDistributionHour/queryCustom", parms)
        .then((response) => {
          console.log(1);
          this.Daydata = response.data;
          console.log(this.Daydata);
        })
        .catch((error) => {
          console.log(error);
        });

        let parms1 = this.$qs.stringify({
        multipleId: "1,2",
        dataTime: "1638547200",
        dayNum: "30",
        type: "1",
      });
      //请求表格数据
      axios
        .post(
          "http://121.43.111.195/powerStatistics/findMultipleDevicePowerMsg",
          parms1
        )
        .then((response) => {
          console.log(3);
          this.Monthdata = response.data;
          console.log(this.Monthdata);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>


<style scoped>
#title {
  background-color: rgb(219, 219, 219);
}
.el-aside {
  height: 1030px;
}
.kuan {
  width: 100%;
}
#myChart {
  width: 100%;
  height: 500px;
  margin: 0 auto;
  margin-top: 5%;
}

.actived {
  background: #2c5dff;
  color: #fff;
}
#inaside {
  background-color: rgb(241, 241, 239);
}
</style>
